import Sidebar from "../components/Sidebar";
import "./globals.css";
import { useState } from "react";

export const metadata = {
  title: "Camera Manager",
  description: "Manage and monitor your cameras efficiently",
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);

  const toggleSidebar = () => setIsSidebarOpen(!isSidebarOpen);

  return (
    <html lang="en">
      <body className="h-screen overflow-hidden bg-[#343940]">
        <div className="flex h-full">
          {/* Sidebar */}
          <div
            className={`sm:flex ${isSidebarOpen ? "block" : "hidden"} sm:w-64 w-full bg-gray-800 text-white`}
          >
            <Sidebar />
          </div>

          {/* Button to open sidebar on small screens */}
          <button
            className="sm:hidden p-4 text-white bg-blue-500 rounded-md fixed top-4 left-4 z-50"
            onClick={toggleSidebar}
          >
            {isSidebarOpen ? "Close" : "Open"} Sidebar
          </button>

          {/* Main content */}
          <div className="flex-1 overflow-auto p-6 main-content">
            {children}
          </div>
        </div>
      </body>
    </html>
  );
}
